import React from "react"
import { Section, Card, Heading, Button, GifPlayer } from "components"
import { Row, Col } from "antd"
import { Link } from "react-router-dom"
import { Trans } from "@lingui/macro"
import { country } from "App"

export default function Thankyou() {
	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}
	const role = (userDetails.role || {}).code

	return (
		<Section>
			<Row type="flex" justify="center" align="middle">
				<Col lg={12}>
					<Section centered textAlign="center">
						<Link to={`/${country}`}>
							<img alt="" src="https://app.cudy.co/img/logo_live.png" width="100" />
						</Link>
					</Section>
					<Card autoHeight noHover>
						<Section centered textAlign="center">
							<GifPlayer src="https://assets9.lottiefiles.com/temp/lf20_4Jjg2l.json" width={250} height={250} class="mb2em" />
							<Heading
								content={<Trans>You're all set!</Trans>}
								subheader={
									<Trans>
										That's a great step! Now{" "}
										{role === "STDN" ? <Trans>find our best tutors</Trans> : <Trans>go to your profile</Trans>} to get
										started.
									</Trans>
								}
								marginBottom="3em"
							/>
							<Link to={role === "STDN" ? `/${country}/browse/tutors` : `/${country}/profile/me`}>
								<Button type="primary" size="medium" icon="user">
									&nbsp;{" "}
									<Trans>{role === "STDN" ? <Trans>Find tutors now</Trans> : <Trans>Go to your Profile</Trans>}</Trans>
								</Button>
							</Link>
						</Section>
					</Card>
				</Col>
			</Row>
		</Section>
	)
}
